import styled from "styled-components"
import TwitterIcon from '@mui/icons-material/Twitter';
import InstagramIcon from '@mui/icons-material/Instagram';
import EmailIcon from '@mui/icons-material/Email';
import PermPhoneMsgIcon from '@mui/icons-material/PermPhoneMsg';
import {mobile} from "../Res"

const Container = styled.div`
    display:flex;
    background-color: #FFEFCB;
    ${mobile({ flexDirection: "column" })}
`;
const Left = styled.div`
    flex:1;
    display: flex;
    flex-direction: column;
    padding: 20px;
`;

const Logo = styled.h1`
`;
const Desc = styled.p`
    margin: 20px 0px;
`;
const SocialContainer = styled.div`
    display: flex;
`;
const SocialIcon = styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: black;
    display:flex;
    align-content: center;
    justify-content: center;
    margin-right: 20px;
    cursor:pointer;
`;

const Center = styled.div`
    flex:1;
    padding: 20px;
    ${mobile({ display: "none" })}
`;

const Title = styled.h3`
    margin-bottom: 30px;
`
const List = styled.ul`
    margin:0;
    padding:0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    cursor:pointer;
`
const ListItem = styled.li`
    width: 50%;
    margin-bottom: 10px;
`

const Right = styled.div`
    flex:1;
    padding: 20px;
    ${mobile({ backgroundColor: "beige" })}
`;

const ContactItem = styled.div`
    margin-bottom: 20px;
    display:flex;
    align-items: center;
`;


const Footer = () => {
  return (
    <Container>

        <Left>
            <Logo> kohi. </Logo>
        
            <Desc>
            The opportunity to experience Coffee and Tea from all over Japan every month
            </Desc>
            <Desc>
            
            </Desc>
            <SocialContainer>
                <SocialIcon>
                    <TwitterIcon/>
                </SocialIcon>
                <SocialIcon>
                    <InstagramIcon/>
                </SocialIcon>
            </SocialContainer>
        </Left>

        <Center>
            <Title>Links</Title>
            <List>
                <ListItem>Home</ListItem>
                <ListItem>Cart</ListItem>
                <ListItem>Our Story</ListItem>
                <ListItem>Terms and Conditions</ListItem>
                <ListItem>Privacy Policy</ListItem>
                <ListItem>Shipping and Returns</ListItem>
            </List>
        </Center>

        <Right>
            <Title>Contact</Title>
            <ContactItem> <PermPhoneMsgIcon style={{marginRight: "10px"}}/> +1 234 567 8910 </ContactItem>
            <ContactItem> <EmailIcon style={{marginRight: "10px"}}/> support@kohi.dev </ContactItem>
        </Right>

    </Container>
  )
}

export default Footer