import styled from "styled-components"
import Navbar from "../components/Navbar"
import Footer from "../components/Footer"
import { Add, Remove } from "@mui/icons-material"
import {mobile} from "../Res"



const Container=styled.div`

`
const Wrapper=styled.div`
    padding: 50px;
    display:flex;
    ${mobile({ padding: "10px", flexDirection:"column" })}
`
const ImageContainer=styled.div`
    flex:1;
`
const Image=styled.img`
    width: 100%;
    height: 90vh;
    object-fit: cover;
    ${mobile({ height: "40vh" })}
`
const InfoContainer=styled.div`
    flex:1;
    padding: 0px 50px;
    ${mobile({ padding: "10px" })}
`
const Title=styled.h1`
    font-weight: 200;

`
const Desc=styled.p`
    margin: 20px 0px;
`
const Price=styled.span`
    font-weight: 100;
    font-size: 40px;
`
const FilterContainer=styled.div`
    width: 50%;
    margin: 30px 0px;
    display: flex;
    justify-content: space-between;
    ${mobile({ width: "100%" })}
`
const Filter=styled.div`
    display:flex;
    align-items: center;

`
const FilterTitle=styled.span`
    font-size: 20px;
    font-weight: 200;
`
const FilterType=styled.select`
    margin-left: 10px;
    padding: 5px;
`
const FilterTypeOption=styled.option`
`;

const AddContainer=styled.div`
    width: 50%;
    display:flex;
    align-items: center;
    justify-content: space-between;
    ${mobile({ width: "100%" })}
`
/* the addition and removal portion */
const AmountContainer=styled.div`
    display: flex;
    align-items: center;
    font-weight: 700;
`
/* this area around and the number itself for the amount ie. a boxed 1*/
const Amount=styled.span`
    width: 30px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px 5px;
`
const Button=styled.button`
    padding: 15px;
    border: 2px solid black;
    background-color: white;
    cursor: pointer;
    font-weight: 500;
    &:hover{
    background-color: #f8f4f4;
  }
`   

const SingProd = () => {
  return (
        <Container>
            <Navbar/>
                <Wrapper>
                    <ImageContainer>
                        <Image src="https://cdn.shopify.com/s/files/1/2666/7672/products/Single_ECUADOR-Arnaud-Causse-Caturra_100_250_2048x2048.jpg?v=1646384949"/>
                    </ImageContainer>
                    <InfoContainer>
                        <Title>Product Description</Title>
                        <Desc>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec
            venenatis, dolor in finibus malesuada, lectus ipsum porta nunc, at
            iaculis arcu nisi sed mauris. Nulla fermentum vestibulum ex, eget
            tristique tortor pretium ut. Curabitur elit justo, consequat id
            condimentum ac, volutpat ornare.
                        </Desc>
                        <Price>$15</Price>
                        <FilterContainer>
                            <Filter>
                                <FilterTitle>Type</FilterTitle>
                                <FilterType>
                                    <FilterTypeOption>Matcha</FilterTypeOption>
                                    <FilterTypeOption>Coffee</FilterTypeOption>
                                    <FilterTypeOption>Tea</FilterTypeOption>
                                </FilterType>
                            </Filter>
                        </FilterContainer>
                        <AddContainer>
                            <AmountContainer>
                                <Remove/>
                                <Amount>1</Amount>
                                <Add/>
                            </AmountContainer>
                            <Button>Add to Cart</Button>
                        </AddContainer>
                    </InfoContainer>
                </Wrapper>
            <Footer/>
        </Container>
  )
}

export default SingProd