import { FlashOff, NoEncryption, Search } from '@mui/icons-material'
import { Badge } from '@mui/material'
import React from 'react'
import styled from 'styled-components'
import LocalMallOutlinedIcon from '@mui/icons-material/LocalMallOutlined';
import {Link} from "react-router-dom"
import {mobile} from "../Res"

const Container = styled.div `
    height: 60px;
    ${mobile({ height: "50px" })}

`;

const Wrapper = styled.div `
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    ${mobile({ padding: "10px 0px" })}
`;

/* added flex : 1 so all the divs can be equal in sizing apart */

const Left = styled.div`
    flex:1;
    display: flex;
    align-items:center;
`;
/* adding language function */
const Language = styled.span`
    font-size: 14px;
    cursor: pointer;
    ${mobile({ display: "none" })}
`;
/* adding search container - probably will move this later*/
const SearchContainer = styled.div`
    border: 0.5px solid lightgray;
    display:flex;
    align-items: center;
    margin-left: 25px;
    padding: 5px;

`;
const Input = styled.input`
    border: none;
    ${mobile({ width: "50px" })}
`;

const Center = styled.div`
    flex:1;
`;

/* adding logo here with h1 sizing */
const Logo = styled.h1`
    font-weight:bold;
    text-align: center;
    color:black;
    ${mobile({ fontSize: "24px" })}
`;

const Right = styled.div`
    flex:1;
    display:flex;
    align-items: center;
    justify-content: flex-end;
    ${mobile({ flex: 2, justifyContent: "center" })}
`;
/* did margin-left to space out the items to the left */
const MenuItem = styled.div`
    font-size: 14px;
    cursor: pointer;
    margin-left: 25px;
    ${mobile({ fontSize: "12px", marginLeft: "10px" })}
`;

const Navbar = () => {
  return (

    <Container>
        <Wrapper>

            <Left>
                <Language>
                    JP | EN
                </Language>
                <SearchContainer>
                    <Input/>
                    <Search style = {{color: "gray", fontSize:16}}/>
                </SearchContainer>
            </Left>

            <Center>
            <Link to={`/Kohi`} style={{textDecoration: 'none'}}>
                <Logo>
                    kohi.
                </Logo>
            </Link>
            </Center>

            <Right>

            <Link to={`/register`} style={{textDecoration: 'none'}}>
                <MenuItem> Register </MenuItem>
            </Link>

            <Link to={`/login`} style={{textDecoration: 'none'}}>
                <MenuItem> Sign in </MenuItem>
            </Link>
            <Link to={`/cart`} style={{textDecoration: 'none'}}>
                <MenuItem>
                        <Badge badgeContent={2} color="primary">
                            <LocalMallOutlinedIcon/>
                        </Badge>
                </MenuItem>
            </Link>
            </Right>
        </Wrapper>     
    </Container>
           
  )
}

export default Navbar